<template>
  <div class="fe-menu">
    <!--系统信息部分-->
    <div class="fe-menu__logoInfor">
      <img :src="systemInforC.logo">
      <template v-if="systemInforC.name">
        <span class="separation">|</span>
        <span class="companyName">{{ systemInforC.name }}</span>
      </template>
    </div>

    <!--一级导航部分-->
    <div class="fe-menu__tabs">
      <div v-if="tabsC.length" class="fe-menu__tabs__content">
        <div
          v-for="(item, index) in tabsC"
          :key="index"
          :class="[item.isActive && 'isActive']"
          @click="clickTab(item, index)"
        >
          <span>{{ item.value }}</span>
        </div>
      </div>
    </div>

    <!--功能区-->
    <div class="fe-menu__operations">
      <div v-if="operationsC.hasMore" class="more">
        <fe-drop class="fe-menu__drop">
          <svg class="moreIcon fe-pointer" width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <circle cx="4" cy="12" r="2" fill="#333333" />
            <circle cx="12" cy="12" r="2" fill="#333333" />
            <circle cx="20" cy="12" r="2" fill="#333333" />
          </svg>
          <div slot="tip" class="fe-menu__dropMenu">
            <div
              v-for="(item, index) in dropTips"
              :key="index"
              class="fe-menu__dropItem"
              @click="clickOperation(item, index)"
            >
              <el-tooltip v-if="item.isLogo" effect="dark" :content="item.tip" placement="left">
                <img :src="item.logo" class="fe-pointer">
              </el-tooltip>
              <template v-else>
                <el-tooltip v-if="item.value.length > 6" effect="dark" :content="item.value" placement="left">
                  <div>{{ item.value.slice(0, 6) }}...</div>
                </el-tooltip>
                <div v-else>
                  {{ item.value }}
                </div>
              </template>
            </div>
          </div>
        </fe-drop>
        <template v-if="operationsC.isAllBtn">
          <template v-for="(item, index) in operationsC.content">
            <span v-if="index < 1" :key="index" class="fe-ml10 fe-menu__btn" @click="clickOperation(item, index)">
              {{ item.value }}
            </span>
          </template>
        </template>
        <template v-else>
          <template v-for="(item, index) in operationsC.content">
            <template v-if="index < 2">
              <el-tooltip
                v-if="item.isLogo"
                :key="index"
                effect="dark"
                :content="item.tip"
                placement="bottom"
              >
                <img :src="item.logo" class="fe-pointer fe-ml10" @click="clickOperation(item, index)">
              </el-tooltip>
              <span v-else :key="index" class="fe-menu__btn fe-ml10" @click="clickOperation(item, index)">
                {{ item.value }}
              </span>
            </template>
          </template>
        </template>
      </div>

      <template v-else>
        <template v-for="(item, index) in operationsC.content" @click="clickOperation(item, index)">
          <span :key="index" class="fe-ml10">
            <el-tooltip
              v-if="item.isLogo"
              :key="index"
              effect="dark"
              :content="item.tip"
              placement="bottom"
            >
              <img :src="item.logo" class="fe-pointer">
            </el-tooltip>
            <span v-else class="fe-menu__btn fe-ml10">{{ item.value }}</span>
          </span>
        </template>
      </template>
    </div>

    <!--用户信息区-->
    <div class="fe-menu__userInfor">
      <fe-drop>
        <div class="info">
          <img v-if="userInforC.logo" :src="userInforC.logo">
          <span class="fe-menu__userInfor__name">{{ userInforC.name }}</span>
        </div>
        <div
          v-if="userInforC.others && userInforC.others.length"
          slot="tip"
          class="fe-menu__dropMenu"
        >
          <div
            v-for="(item, index) in userInforC.others"
            :key="index"
            :class="['fe-menu__dropItem']"
            @click="clickUserOther(item, index)"
          >
            <img v-if="item.logo" :src="item.logo">
            <span>{{ item.value }}</span>
          </div>
        </div>
      </fe-drop>
    </div>
  </div>
</template>

<script>

import ElTooltip from 'element-ui/lib/tooltip';
import 'element-ui/lib/theme-chalk/tooltip.css';
import FeDrop from './comps/drop/index';

export default {
  name: 'FeMenu',
  components: {
    ElTooltip,
    FeDrop,
  },
  props: {
    // 系统信息
    systemInfor: {
      type: Object,
      default: () => {
        return {
          logo: '', // 远程路径或require的图片
          name: '', // 系统名称
        };
      },
    },
    // 一级导航
    tabs: {
      type: Array,
      default() {
        // [{value}] // 展示内容
        return [];
      },
    },
    // 操作区  Array<{value: String} 或 {logo: String, tip: String}>，分别对应文字和图标
    operations: {
      type: Array,
      default() {
        return [];
      },
    },
    // {name, logo, others: [{value}]} 名称、图标、下拉功能
    userInfor: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  data() {
    return {
      // 组件中使用的系统信息
      systemInforC: {},
      // 组件中使用的一级导航
      tabsC: [],
      // 组件中使用的二级导航
      operationsC: {},
      // 组件中使用的用户信息
      userInforC: {
        name: '',
        logo: '',
        others: [],
      },
    };
  },
  computed: {
    dropTips() {
      return this.operationsC.isAllBtn ? this.operationsC.content.slice(1) : this.operationsC.content.slice(2);
    },
  },
  created() {
    this.initComponentData();
  },
  methods: {
    // 初始化组件数据
    initComponentData() {

      this.systemInforC = {
        ...this.systemInfor,
        logo: this.systemInfor.logo || require('../assets/imgs/logo.png'), // 添加默认logo
      };

      this.tabsC = this.tabs.map(
        item => {
          return {
            ...item,
            isActive: [true, false].includes(item.isActive) ? item.isActive : false, // 是否激活状态
          };
        },
      );

      this.operations.forEach(
        (item, index) => {
          // logo没有提示，进行报警提示
          if (item.logo && !item.tip) {
            console.warn(`the ${index} logo should have a tip in operations`);
          }
        },
      );
      // 是否存在更多按钮展示
      this.operationsC.hasMore = this.operations.length > 3 ||
          (this.operations.length > 2 && this.operations.every(item => item.value));
      // 是否全部为按钮
      this.operationsC.isAllBtn = this.operations.every(item => item.value);
      this.operationsC.content = this.operations.map(item => {
        return {
          ...item,
          isLogo: !!item.logo, // 是否为logo
          isBtn: !!item.value, // 是否为按钮
        };
      });

      this.userInforC.name = this.userInfor.name;
      this.userInforC.logo = this.userInfor.logo || require('../assets/imgs/user.png');
      if (this.userInfor.others && this.userInfor.others.length) {
        this.userInforC.others = this.userInfor.others.map(
          item => {
            return {
              ...item,
            };
          },
        );
      }
    },
    clickTab(item, index) {
      this.tabsC.forEach(
        (val, key) => {
          val.isActive = key === index;
        },
      );
      this.$emit('clickTab', item, index);
    },
    clickOperation(item, index) {
      this.$emit('clickOperation', item, index);
    },
    clickUserOther(item, index) {
      this.userInforC.others && this.userInforC.others.forEach(
        (item, key) => {
          item.isActive = key === index;
        },
      );
      this.$emit('clickUserOther', item, index);
    },
  },
};
</script>
