<template>
  <div style="display: inline;" @mouseenter="mouseenter" @mouseleave="mouseleave">
    <div ref="ref">
      <slot></slot>
    </div>
    <div
      ref="tooltip"
      :style="{
        visibility: isShowTip && $slots.tip ? 'visible' : 'hidden',
        backgroundColor: '#fff',
        border: '1px solid #ebeef5',
        borderRadius: '4px',
        boxShadow: '0 2px 12px 0 rgb(0 0 0 / 10%)'
      }"
    >
      <slot name="tip"></slot>
    </div>
  </div>
</template>

<script>
import { createPopper } from '@popperjs/core';
export default {
  name: 'FeDrop',
  props: {
    name: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      timer: null,
      isShowTip: false,
    };
  },
  mounted() {

    const button = this.$refs.ref;
    const tooltip = this.$refs.tooltip;

    createPopper(button, tooltip, {
      placement: 'bottom',
      modifiers: [
        {
          name: 'offset',
          options: {
            offset: [0, 20],
          },
        },
      ],
    });
  },
  methods: {
    mouseenter() {
      this.isShowTip = true;
      clearTimeout(this.timer);
    },
    mouseleave() {
      this.timer = setTimeout(
        () => {
          this.isShowTip = false;
        },
        300,
      );
    },
  },
};
</script>

