import Vue from 'vue';
import Router from 'vue-router';
import TopNavLayout from './layout/top-nav-layout/index.vue';
import SideNavLayout from './layout/side-nav-layout/index.vue';

import components from '../components.json';
const children = Object.keys(components).map((componentName) => {
  return {
    path: componentName,
    name: 'components-' + componentName,
    component: (resolve) => require([`../packages/${componentName}/index.md`], resolve),
  };
});

const files = require.context('./docs/', true, /.md/);
const docs = files.keys().map((docName) => {
  let name = docName.replace(/(\.\/|\.md)/g, '');
  return {
    path: name,
    name: 'docs-' + name,
    component: (resolve) => require([`./docs/${name}.md`], resolve),
  };
});

const designFiles = require.context('./design/', true, /.md/);
const design = designFiles.keys().map((docName) => {
  let name = docName.replace(/(\.\/|\.md)/g, '');
  return {
    path: name,
    name: 'design-' + name,
    component: (resolve) => require([`./design/${name}.md`], resolve),
  };
});

Vue.use(Router);

let routes = [
  {
    path: '/',
    redirect: '/fehorizon-pc-ui',
  },
  {
    path: '/',
    component: TopNavLayout,
    children: [
      // 官网
      {
        path: '/fehorizon-pc-ui',
        name: 'fehorizon-pc-ui',
        component: (resolve) => require(['./views/home/fehorizon-pc-ui.vue'], resolve),
      },
      // 更新日志
      {
        path: '/changelog',
        name: 'changelog',
        component: (resolve) => require(['./views/changelog/index.vue'], resolve),
      },
      // 文档
      {
        path: '/docs',
        component: SideNavLayout,
        redirect: '/docs/install',
        children: [...docs],
      },
      // 文档
      {
        path: '/design',
        component: SideNavLayout,
        redirect: '/design/font',
        children: [...design],
      },
      // 组件
      {
        path: '/components',
        component: SideNavLayout,
        redirect: '/components/layout',
        children: [
          ...children,
        ],
      },
    ],
  },
];

export default new Router({
  base: process.env.BASE_URL,
  routes, 
});
