<template>
  <div class="demo-block" :class="blockClass">
    <!--效果展示-->
    <div class="source">
      <slot name="source"></slot>
    </div>

    <div v-if="$slots.default" class="description">
      <slot></slot>
      <div class="demo-block-control">
        <!-- <svg-icon icon-class="openOnline"></svg-icon> -->
        <svg-icon
          ref="copy"
          icon-class="copy"
          @click.native="copyCode"
          @mouseenter.native="copyMouseenter"
          @mouseleave.native="copyMouseleave"
        ></svg-icon>
        <svg-icon
          icon-class="code"
          @click.native="isExpanded = !isExpanded"
          @mouseenter.native="codeMouseenter"
          @mouseleave.native="codeMouseleave"
        ></svg-icon>
        <div v-show="showMemo" ref="memo" class="memo">
          {{ memo }}
        </div>
      </div>
    </div>
    <div ref="meta" class="meta">
      <div class="highlight">
        <slot name="highlight"></slot>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isExpanded: false,
      showMemo: false,
      memo: '',
    };
  },
  computed: {
    blockClass() {
      return `demo-${this.$route.name}`;
    },
    codeArea() {
      return this.$el.getElementsByClassName('meta')[0];
    },
    codeAreaHeight() {
      return this.$el.getElementsByClassName('highlight')[0].clientHeight + 20;
    },
  },
  watch: {
    isExpanded(val) {
      this.codeArea.style.height = val ? `${this.codeAreaHeight + 1}px` : '0';
    },
  },
  methods: {
    copyMouseenter() {
      this.memo = '拷贝代码';
      this.showMemo = true;
      this.$refs.memo.style.right = '-10px';

    },
    copyMouseleave() {
      this.showMemo = false;
    },
    codeMouseenter() {
      this.memo = '展示代码';
      this.showMemo = true;
      this.$refs.memo.style.right = '-40px';
    },
    codeMouseleave() {
      this.showMemo = false;
    },
    copyCode() {
      this.copy(this.$refs.meta.innerText);
      this.memo = '拷贝成功';
    },
    copy(text) {
      const textString = text.toString();
      let input = document.querySelector('#copy-input');
      if (!input) {
        input = document.createElement('textarea');
        input.id = 'copy-input';
        input.readOnly = 'readOnly';        // 防止ios聚焦触发键盘事件
        input.style.position = 'absolute';
        input.style.left = '-1000px';
        input.style.zIndex = '-1000';
        document.body.appendChild(input);
      }
      input.value = textString;
      // ios必须先选中文字且不支持 input.select();
      this.selectText(input, 0, textString.length);
      if (document.execCommand('copy')) {
        document.execCommand('copy');
        // TODO 复制成功提示
      }
      input.blur();
    },
    selectText(textbox, startIndex, stopIndex) {
      if (textbox.createTextRange) { // ie
        const range = textbox.createTextRange();
        range.collapse(true);
        range.moveStart('character', startIndex);// 起始光标
        range.moveEnd('character', stopIndex - startIndex);// 结束光标
        range.select();// 不兼容苹果
      } else { // firefox/chrome
        textbox.setSelectionRange(startIndex, stopIndex);
        textbox.focus();
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.demo-block {
  border-radius: 3px;
  transition: 0.2s;
  border: 1px solid #ececec;
  margin-top: 10px;

  & + .demo-block {
    margin-top: 20px;
  }

  .source {
    padding: 20px;
    border-bottom: 1px solid #ececec;
  }

  .description {
    padding: 20px;
    box-sizing: border-box;
    border-bottom-left-radius: 3px;
    border-bottom-right-radius: 3px;
    font-size: 14px;
    line-height: 22px;
    color: #666;
    word-break: break-word;
    background-color: #fafafa;
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    p {
      margin: 0;
    }

    .demo-block-control {
      margin-left: 10px;
      width: 106px;
      height: 30px;
      border-radius: 15px;
      box-sizing: border-box;
      background-color: #fff;
      color: #409eff;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      .memo{
        position: absolute;
        right: -40px;
        bottom: -25px;
        width: 66px;
        height: 26px;
        text-align: center;
        line-height: 26px;
        color: #fff;
        background: rgba(0, 97, 255, 0.9);
        border-radius: 4px;
      }
      ::v-deep .svg-icon {
        width: 12px;
        height: 12px;
        cursor: url('../assets/img/mouse.png'), auto;
        fill: #000;
        &:hover {
          fill: #0061ff;
        }
        &:nth-child(n + 2) {
          margin-left: 18px;
        }
      }
    }
  }

  .meta {
    overflow: hidden;
    height: 0;
    transition: height 0.2s;

    .highlight {
      margin-top: 10px;
    }
  }
}
</style>
