<template>
  <div
    class="fe-button"
    :type="type"
    :class="[
      `fe-button--${type}`,
      size ? 'fe-button--' + size : '',
      disabled || loading ? 'is-disabled' : '',
      loading ? 'is-loading' : '',
      round ? 'is-round' : ''
    ]"
    @click="handleClick"
  >
    <img v-if="loading" src="../assets/png/loading.png" class="fe-button__loading">
    <slot></slot>
  </div>
</template>
<script>
export default {

  name: 'FeButton',

  props: {
    type: {
      type: String,
      validator: function(value) {
        // 主要、次要、默认、文字
        return ['major', 'secondary', 'default', 'text'].includes(value);
      },
      default: 'default',
    },
    size: {
      type: String,
      validator: function(value) {
        // 大、中、小、最小
        return ['large', 'medium', 'small', 'mini'].includes(value);
      },
      default: 'medium',
    },
    // 是否显示loading
    loading: Boolean,
    // 是否禁止操作
    disabled: Boolean,
    // 是否带有圆角
    round: Boolean,
  },

  methods: {
    handleClick(evt) {
      if (this.disabled || this.loading) {
        return;
      }
      this.$emit('click', evt);
    },
  },
};
</script>
