<template>
  <div class="fe-tabs" :class="[type]">
    <slot></slot>
  </div>
</template>

<script>

export default {
  name: 'FeTabs',
  props: {
    // 没有传递值的时候要选中第一个子节点
    value: {
      require: false,
      type: String,
      default: undefined,
    },
    // 类型 default,card
    type: {
      require: false,
      type: String,
      validate(val) {
        return ['card'].includes(val);
      },
      default: 'default',
    },
  },
  methods: {
    setSelect(name, label, event) {

      this.$emit('tab-click', name, label, event);

      this.$emit('input', name);
    },
  },
};
</script>
