<template>
  <div
    class="fe-tab-pane"
    :class="[
      active&&'active',
      disabled&&'disabled',
      $parent.type,
    ]"
    @click="select($event)"
  >
    <slot
      v-if="$slots.default"
      :disabled="disabled"
      :active="active"
    ></slot>
    <div v-else class="content">
      {{ label }}
    </div>
  </div>
</template>

<script>

export default {
  name: 'FeTabPane',
  props: {
    label: {
      require: false,
      type: String,
      default: '',
    },
    name: {
      require: false,
      type: String,
      default: '',
    },
    disabled: {
      require: false,
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      isShow: true,
    };
  },
  computed: {
    active() {
      let activeValue = this.$parent.value;
      return activeValue !== undefined ? this.name === activeValue : this.$parent.$children[0] === this;
    },
  },
  methods: {

    select(event) {
      if (this.disabled || this.active) {
        return;
      }
      this.$parent.setSelect(this.name, this.label, event);
    },
  },
};
</script>
