<template>
  <header class="fe-header" :style="{ 'height': height, 'line-height': height, backgroundColor: bgColor, color: color}">
    <slot></slot>
  </header>
</template>

<script>

export default {
  name: 'FeHeader',
  props: {
    height: {
      type: String,
      default: '28px',
    },
    bgColor: {
      type: String,
      default: '#333333',
    },
    color: {
      type: String,
      default: '#fff',
    },
  },
};
</script>
