<template>
  <div ref="layout" class="examples-sidenav-layout">
    <div class="examples-layout__view">
      <div class="examples-layout__nav">
        <side-nav />
      </div>
      <div
        ref="layoutContent"
        class="examples-layout__content"
      >
        <router-view></router-view>
        <Footer></Footer>
      </div>
      <ul class="anchor-box">
        <li
          v-for="(item,index) in anchorList"
          :key="index"
          class="anchor"
          :class="{active: index === activeIndex}"
          @click="anchorScrollInto(item,index)"
        >
          {{ item.innerText }}
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import sideNav from './side-nav.vue';
import Footer from 'examples/views/home/footer.vue';

export default {
  components: {
    sideNav,
    Footer,
  },
  data() {

    const scrollCb = () => {

      setTimeout(
        () => {
          if (this.clickScroll) {
            return;
          }

          this.anchorList.find(
            item => {
              let res = item.getBoundingClientRect().top - 58;
              if (res > 0 && res < 58 + 50) {

                this.activeIndex = this.anchorList.indexOf(item);
                return true;
              }
            },
          );
        },
      );
    };

    return {
      anchorList: [],
      activeIndex: 0,
      io: null,
      clickScroll: false,
      timeBar: null,
      scrollCb: scrollCb.bind(this),
    };
  },
  watch: {

    $route: {
      handler() {

        const layout = this.$refs.layout;

        if (layout) {
          this.$nextTick(() => {
            layout.scrollTop = 0;
          });
        }

        this.activeIndex = 0;

        this.$nextTick(() => {

          document.querySelector('.examples-sidenav-layout').removeEventListener(
            'scroll',
            this.scrollCb,
          );

          this.anchorList = [...this.$el.querySelectorAll('a.header-anchor')].filter(
            v => v.parentElement.tagName === 'H3').map(v => v.parentElement,
          );

          document.querySelector('.examples-sidenav-layout').addEventListener(
            'scroll',
            this.scrollCb,
          );
        });
      },
      immediate: true,
    },
  },
  methods: {
    anchorScrollInto(el, index) {
      this.clickScroll = true;
      this.activeIndex = index;
      el.scrollIntoView();
      clearTimeout(this.timeBar);
      this.timeBar = setTimeout(() => {
        this.clickScroll = false;
      }, 100);
    },
  },
};
</script>

<style lang="scss" scoped>
@import './../../style/mixins/mixins.scss';

$nav-width: 300px;
.examples-sidenav-layout {
  @include scroll-bar;
  position: relative;
  display: block;
  height: calc(100vh - 58px);
  width: 100%;
  overflow: auto;
  overflow: overlay;

  .examples-layout__view {
    width: 100%;

    &.is-component {
      min-width: 1366px;
    }
  }

  .examples-layout__nav {
    @include scroll-bar;
    position: fixed;
    width: $nav-width;
    min-width: $nav-width;
    top: 0;
    padding-top: 58px;
    height: 100%;
    overflow-y: auto;
    overflow-y: overlay;
    z-index: 10;
  }

  .examples-layout__content {
    position: relative;
    width: 100%;
    padding: 20px 0 20px calc($nav-width + 30px);

    ::v-deep .footer {
      position: absolute;
      bottom: 0;
      left: calc($nav-width + 30px);
      width: calc(100% - $nav-width - 30px);

      .content {
        padding: 0 30px;
      }
    }

    .fehorizon-pc-doc {
      padding-bottom: 352px;
      width: 750px;
    }
  }

  .anchor-box {
    position: fixed;
    top: 78px;
    right: 70px;
    width: 120px;
    padding: 10px;
    box-sizing: border-box;
    background-color: #fff;

    .anchor {
      padding: 0 9px;
      color: #333;
      font-size: 12px;
      line-height: 24px;
      cursor: pointer;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;

      &.active {
        background: #F6F6F6;
        border-radius: 4px;
        color: #0061FF;
      }
    }


  }
}
</style>
