<template>
  <section class="fe-layout" :class="{ 'is-vertical': isVertical }" :style="{ backgroundColor: bgColor }">
    <slot></slot>
  </section>
</template>

<script>
export default {
  name: 'FeLayout',

  props: {

    direction: {
      type: String,
      default: '',
    },
    bgColor: {
      type: String,
      default: '#ececec',
    },
  },

  computed: {
    isVertical() {
      if (this.direction === 'vertical') {
        return true;
      } if (this.direction === 'horizontal') {
        return false;
      }
      return this.$slots && this.$slots.default
        ? this.$slots.default.some(vnode => {
          const tag = vnode.componentOptions && vnode.componentOptions.tag;
          return tag === 'fe-header';
        })
        : false;
    },
  },
};
</script>
